/********************* */
/* COLORS MIXINS */
/********************* */

//@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700")
@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700,900');

$font-stack:  'Lato', Helvetica, Arial,  sans-serif;

$orange: #EF7A23;
$lightgray: #FAF8F5;
$blue: #016499;
$HeadlineColor: #7D7D7D;
$bodyColor: #747474;


@mixin bluebutton {
  background: rgba(1,100,153,0.79);
  font-size: 1.5rem;
  color: #FFFFFF; }

@mixin whitebutton {
  background: rgba(255,255,255,0.98);
  color: $orange;
  font-size: 1.5rem; }

@mixin cssTransition($secs) {
  -webkit-transition: all $secs ease-in-out;
  -moz-transition: all $secs ease-in-out;
  -o-transition: all $secs ease-in-out;
  -ms-transition: all $secs ease-in-out;
  transition: all $secs ease-in-out; }


/********************* */
/* GENERAL STYLES */
/********************* */



// Sets REM root size to 16px
html {
  font-size: 100%; }

body {
  font-family: $font-stack; }

/********************* */
/* LINK STYLES */
/********************* */
a, a:visited {
  // Mobile tap color
  &:link {
    //Highlight links on iOS, Similar to :hover for mobile devices.
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3); } }

/********************* */
/* H1, H2, H3, H4, H5 P STYLES */
/********************* */
h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5 {
  font-family: $font-stack;
  font-weight: bold;
  text-transform: uppercase;

  // Removing text decoration from all headline links
  a {
    text-decoration: none; } }

h1, .h1 {}

h2, .h2 {}

h3, .h3 {}

h4, .h4 {}

h5, .h5 {}



/********************* */
/* HEADER STYLES */
/********************* */
.header {
  ul.off-canvas-list li {
    list-style: none; } }

/********************* */
/* NAVIGATION STYLES */
/********************* */
.top-bar {
  .title-area {
    z-index: 1; } }

ul#menu-main-menu-1 {
  margin: 2rem 0 0;
  li {
    a {
      font-size: 1rem;
      color: #B4B0A5;
      text-transform: uppercase; } } }

// Drop down arrows
.dropdown.menu.medium-horizontal > li.is-dropdown-submenu-parent > a::after {
  border-color: #b4B0A5 transparent transparent; }

.off-canvas-list {
  ul {
    margin-left: 0;
    li {
      a {
        border-bottom: 0px; } }

    .dropdown {
      margin-left: 20px; } } }


/********************* */
/* HERO SECTION */
/********************* */

#hero {
  background-size: cover;
  background-position: 100%, 0%;
  height: 550px;
  position: relative;
  .heroCTA {
    width: 450px;
    float: right;
    margin: 5rem 200px 0 0;
    color: #fff;
    h2 {
      font-family: "Lato", sans-serif;
      font-size: 2.25rem;
      line-height: 2.75rem;
      color: #FFFFFF;
      font-weight: 600;
      text-transform: capitalize; }
    p {
      font-size: 1.2rem; }
    .button {
      @include whitebutton; } }
  .arc {
    position: absolute;
    left: 0;
    bottom: 0; } }



/********************* */
/* POSTS & CONTENT STYLES */
/********************* */
#content {
  #inner-content {
    padding: 1rem 0rem; } }

.single-title,
.page-title {}

